<template>
    <yd-layout class="box_xunjiandian">
        <navBar title="巡检点" slot="navbar"></navBar>
        <div class="box_cont">
            <div class="flex box_borderb">
                <div>巡检点名称：</div>
                <p class="gray">{{name}}</p>
            </div>
            <div class="flex">
                <div>班&nbsp;&nbsp;&nbsp;次：</div>
                <div style="flex: 1;">
                    <yd-radio-group v-model="radio" color="#1D91E6" v-if="showbanci">
                        <yd-radio v-for="item,index in data" :key="index" :val="item.BANCI_ID" @click.native="onClickBanci(item.BANCI_NAME)">{{item.BANCI_NAME}}</yd-radio>
                    </yd-radio-group>
                </div>
            </div>
            <div class="flex" style="padding:.24rem;" v-if="isShowButton">
                <div class="box_btn">
                    <yd-button bgcolor="#fff" color="#1D91E6" size="large" class="box_btn1" @click.native="onClickPress(0)">查看历史</yd-button>
                </div>
                <div style="flex: 1;">
                    <yd-button bgcolor="#1D91E6" color="#fff" size="large" class="box_btn2" @click.native="onClickPress(1)">开始巡检</yd-button>
                </div>
            </div>
            <div class="flex" style="padding:.24rem;" v-if="!isShowButton">
                <div class="box_btn">
                    <yd-button bgcolor="#fff" color="#1D91E6" size="large" class="box_btn1">查看历史</yd-button>
                </div>
                <div style="flex: 1;">
                    <yd-button bgcolor="#1D91E6" color="#fff" size="large" class="box_btn2">开始巡检</yd-button>
                </div>
            </div>
        </div>
    </yd-layout>
</template>

<script>
    import {api_xunjian_info,api_banci_options} from "../../components/APIKeys";

    export default {
        name: "xunjiandian_kaishi",
        data() {
            return {
                radio:1,//班次单选
                banciname:'',
                showbanci:false,
                // token:window.localStorage.getItem('token'),
                name:'',//巡检点名称
                id:window.localStorage.getItem('checkLocationId'),//巡检点ID
                data:[],//班次
                isShowButton:true,//是否显示可点击按钮
            }
        },
        mounted(){
            this.doLoadInfo();
            this.doLoadOptions();

        },
        methods: {
            //点击单选name赋值
            onClickBanci(name){
                this.banciname = name;
            },
            //点击跳转页面
            onClickPress(index){
                window.localStorage.setItem('banciId',this.radio);
                window.localStorage.setItem('banciname',this.banciname);
                window.localStorage.setItem('xunjiandianId',this.id);

               if(index == 0){
                   this.$pushRoute('HistoryList');
               }else{
                   this.$pushRoute('xunjiandian');
               }
            },
            //获取巡检点信息
            doLoadInfo(){
                this.$showDialog();
                let jsonData = {
                  // token:this.token,
                  xunjiandian_id:this.id,
                  // xunjiandian_id:1,
              };
              this.$post(api_xunjian_info, jsonData).then(res=>{
                  this.$showDialog(false);
                  if(res.errno == 0){
                      this.name = res.data.xjdmc;
                      // this.id = res.data.xunjiandian_id;
                  }else{
                      this.$showError(res.errmsg);
                      this.isShowButton = false;
                  }
              })
            },
            doLoadOptions(){
               this.$post(api_banci_options, {token:this.token}).then(res=>{
                    if(res.errno == 0){

                        if( res.data.length > 0){
                            this.radio =  res.data[0].BANCI_ID;
                            this.banciname = res.data[0].BANCI_NAME;
                        }
                        this.data = res.data || [];
                        this.showbanci = true;
                    }else{
                        this.$showError('获取班次失败');
                    }
               })
            }
        }
    }
</script>

<style scoped>
    .box_cont{
        margin: .24rem;
        background: #fff;
    }
    .flex{
        display: flex;
        padding: .3rem .24rem .24rem .24rem;
    }
    .box_borderb{
        border-bottom: 1px solid #d9d9d9;
    }
    .gray{
        color: gray;
    }
    .box_btn{
        flex: 1;
        margin-right: .5rem;
    }
    .box_btn1{
        border: 1px solid #1D91E6;
        border-radius:5px;
    }
    .box_btn2{
        border-radius:5px;
    }


</style>
<style>
    .box_xunjiandian .yd-radio>input[type=radio]:checked+.yd-radio-icon{
        margin-bottom: .03rem;
    }
    .box_xunjiandian .yd-radio-icon{
        margin-bottom: .03rem;
    }
    .box_xunjiandian .yd-radio{
        padding: 0 2rem .3rem .3rem;
    }
    .box_xunjiandian .yd-radio-text{
        font-size: .3rem;
    }
</style>



